* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
body {
  margin: 10px;
  background-color: #2d3436;
  background-image: linear-gradient(315deg, #000000 0%, #2d3436 74%);
  text-align: left;
  font-family: "Unica One", cursive;
  background-repeat: no-repeat;
  background-attachment: fixed;
  color: white;
}
.center {
  text-align: center;
  font-family: "Unica One", cursive;
}
.form {
  display: flex;
  flex-direction: column;
  height: 220px;
  font-family: "Unica One", cursive;
  align-items: center;
  justify-content: center;
  width: 80%;
  margin: auto;
}
.label {
  font-size: 16px;
  align-self: start;
  padding: 2px;
  font-weight: 100;
}
.search {
  padding: 10px;
  width: 100%;
  font-family: "Unica One", cursive;
  border-radius: 3px;
  border: none;
}
button {
  margin: 10px auto;
  padding: 10px;
  width: 100%;
  border-radius: 3px;
  border: none;
  font-family: "Unica One", cursive;
  background-color: white;
  color: black;
  font-size: 17px;
  text-align: center;
}
.card {
  border: none;
  background-color: #000000;
  background-image: url(background.jpg);
  background-size: contain;
  border-radius: 6px;
  margin: 20px;
  margin-bottom: 30px;
  border: 3px dashed white;
  box-shadow: 0px 1px 10px 3px rgba(120, 120, 120, 0.1);
  padding: 10px;
  display: flex;
  text-align: left;
  flex-direction: column;
}
.card:last-child {
  margin-bottom: 60px;
}
.poster {
  width: 75%;
  align-self: center;
  border-radius: 4px;
  margin: 10px;
}
.title {
  font-size: 34px;
  font-weight: 700;
  font-family: "Unica One", cursive;
}
.author {
  border: 2px dashed white;
  padding: 2px;
  margin-top: 2px;
  margin-bottom: 5px;
}
.subtitle {
  font-size: 14px;
  font-weight: 300;
}
.genre {
  background-color: rgb(143, 64, 27);
  color: white;
  padding: 2px 5px;
  border-radius: 2px;
}
.page {
  margin-top: 4px;
}
.desc {
  font-weight: 300;
  font-family: "Roboto", sans-serif;
}
h6 {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  font-family: "Roboto", sans-serif;
  background-color: #0b0b0b;
  color: white;
  text-align: center;
  font-weight: 300;
  font-size: 12px;
  padding: 5px 2px;
}
